<template>
    <van-nav-bar
        :title="title"
        :left-text="show?'返回':''"
        :left-arrow="show"
        @click-left="onClickLeft"
    >   
    <div slot="right" >
        <van-icon name="scan"  @click="startScan" />
        <van-icon v-if="searchShow" name="search"  @click="goSearch" />
    </div>

    </van-nav-bar>
</template>

<script>
export default {
    data(){
        return {
            searchShow:true
        }
    },
    props:{
        title:{
            type:String,
            default:"xxxx"
        },
        show:{
            type:Boolean,
            default:false
        }
    },
    methods:{
        startScan(){
            console.log("startScan")
        },
        goSearch(){
            this.$router.push({name:'search'})
        },
        onClickLeft(){
            this.$store.commit("changeActive",this.$store.state.oldActive);
            this.$router.go(-1);

        },
        onClickRight(){

        }
    },
    mounted(){
        if(this.$route.name=="search"){
            this.searchShow = false;
        }
    }
}
</script>

