

import Vue from "vue";
import Vuex from "vuex"

Vue.use(Vuex);


import axios from "@/utils/axios"

import {FIRSTTODO,COUNTADD,INCREMENT,DECREMENT} from "@/utils/mutationtype";





// 每一个 Vuex 应用的核心就是 store（仓库）  “store”基本上就是一个容器  包含大部分的状态 
// vuex store 仓库  
// Vuex 的状态存储是响应式的  当 Vue 组件从 store 中读取状态的时候   store 中的状态发生变化，那么相应的组件也会相应地得到高效更新
// 你不能直接改变 store 中的状态。改变 store 中的状态的唯一途径就是显式地提交 (commit) mutation

// Vuex 允许我们将 store 分割成模块（module）
const ModuleCar = {
    state:{
        carNum:2,
        carList:[]
    },
    actions:{

    },
    mutations:{

    }
}

const ModuleData = {
    state:{
        banner:[],
 
    },
    actions:{
        getBannerAsync({commit},{url,params}){
            axios.get(url,{
                params
              })
              .then(res=>{
                //   自动提交 mutation
                commit("getBanner",res.data.result);
              })
        }
    },
    mutations:{
        getBanner(state,banner){
            state.banner = banner;
        }
    }
}


// 一个对象 state 就包含了全部组件的应用层级状态
const store = new Vuex.Store({
    modules:{
        cars:ModuleCar,
        data:ModuleData
    },
    state:{   // 组件里面所有的数据/状态  Vuex 使用单一状态树 
        count:1906,
        city:'wuhan~',
        msg:"wuhan1906 daydayup",
        userInfo:{

        },
        goodList:[],
        typeList:[],
        searchList:[],
        keyword:"123",
        active:0,
        oldActive:0
    },
    actions:{
        // Action 提交的是 mutation，而不是直接变更状态。
        // Action 可以包含任意异步操作。
        //  Action 函数接受一个与 store 实例具有相同方法和属性的 context 对象  context.commit
        increment(context){
            context.commit(INCREMENT);  // mutations type 
        },
        decrement({commit},num){  // context = context.commit =  {commit:func(){}}  = {commit:commit}
            commit(DECREMENT,num)  // 提交mutations 
        },
        changeMsgAsync({commit},{url,params}){
            axios.get(url,{params}).then(res=>{
                commit("changeMsg",res.data);
            })
        },
        getTypeListAsync({commit},{url,cb}){
            axios.get(url).then(res=>{
                cb();
                commit("getTypeList",res.data.result);
            })
        },
        getSearchListAsync({commit},{url,params}){
            axios.get(url,{
                params
            }).then(res=>{
                commit("getSearchList",res.data.result);
            })
        }
    },
    mutations:{
        // 更改 Vuex 的 store 中的状态的唯一方法是提交 mutation  
        // mutation 都有一个字符串的 事件类型 (type) 和 一个 回调函数 (handler) 
        // 必须是同步函数 不能写 异步 ajax 
        [FIRSTTODO](state){   // store.state;
            state.count++;
        },
        [COUNTADD](state,num){
            state.count+=num;
        },
        countDesc(state,num){
            state.count-=num;
        },
        changeCity(state,city){
            state.city = city;
        },
        [INCREMENT](state){
            state.count++;
        },
        [DECREMENT](state,num){
            state.count -= num;
        },
        changeMsg(state,msg){
            state.msg = msg;
        },
        changeUserInfo(state,userInfo){   // Mutation 需遵守 Vue 的响应规则  新对象替换老对象  
            // state.userInfo = { username : "aaaaa"}   
            state.userInfo = {...state.userInfo,...userInfo};
            // state.userInfo = [...state.userInfo,...userInfo]
        },
        getGoodList(state,goodList){
            state.goodList = goodList;
        },
        getTypeList(state,typeList){
            state.typeList = typeList;
        },
        getSearchList(state,searchList){
            state.searchList = searchList;
        },
        getKeyword(state,keyword){
            state.keyword = keyword;
        },
        changeActive(state,active){
            state.active = active;
        },
        changeOldActive(state,oldActive){
            state.oldActive = oldActive;
        }
       
    },
    getters:{
        // store 中的 state 中派生出一些状态，例如对列表进行过滤并计数  把store.state 数据进行优化 
        newCount(state){  
            return state.count * 10;
        }
    }
})


export default store;