Vue.directive("mymodel",{
    bind(el,binding){
        console.log(binding);
        var obj = new Object();
        obj['value'] = binding.value;
        el.value = obj.value;
        el.oninput = function(e){
            obj.value = e.target.value;
        }
        Object.defineProperty(obj,'value',{
            get(){
                return "old"
            },
            set(val){
                console.log(val);
                el.value = val;
               
            }
        })
    },
    updated(){

    }
})



// 全局混入 

Vue.mixin({
    created(){
        // console.log("这是 全局混入");
    },
    data(){
        return {
            common:{}
        }
    },
    methods:{
        getCommon(types){
            this.$axios.get("/vue/index").then(res=>{
                this.common = res.data + types ; 
                console.log(this.common)
            })
        }
    }

})