<template>
  <div id="app">
    
    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div> -->
    <!-- 路由出口 -->
    <transition :name="moveName" mode="out-in">
        <router-view class="main" />
    </transition>
    
  </div>
</template>



<script>
export default {
  data(){
    return {
      moveName:"slide-list"
    }
  },
  watch:{
    '$route':function(to,from){
        // to 进入的路由
        // from 离开的路由  

        const toLength  = to.path.split("/").length;
        const fromLength = from.path.split("/").length;
        console.log("进入 "+ toLength);
        console.log("离开 "+ fromLength);
        console.log(toLength>fromLength?'列表进入详情':'详情返回列表');

        this.moveName  = toLength>fromLength ? 'slide-detail' : 'slide-list'; 
    }
  }
}
</script>



<style lang="scss" >

// left 左     right 右 

// 列表进入详情  
@keyframes left-out{
    0%{transform: translateX(0);opacity: 1;}
    100%{transform: translateX(-100%);opacity: 0;}
}

@keyframes right-in{
    0%{transform: translateX(100%);opacity: 0;}
    100%{transform: translateX(0%);opacity: 1;}
}



// 详情返回列表 
@keyframes right-out{
    0%{transform: translateX(0%);opacity: 1;}
    100%{transform: translateX(100%);opacity: 0;}
}

@keyframes left-in{
    0%{transform: translateX(-100%);opacity: 0;}
    100%{transform: translateX(0%);opacity: 1;}
}


// 详情 进来
.slide-detail-enter-active{
     animation: right-in .8s;
}

// 列表 离开 
.slide-detail-leave-active{
    animation: left-out .2s;
}


// 列表 进来
.slide-list-enter-active{
   animation: left-in .8s;
}

// 详情离开
.slide-list-leave-active{
    animation: right-out .2s;
}






