<template>
    <div id="guide">
        <SwipeDemo 
            id="swipeone"
            :options="swiperOptions"
        >
            <SwipeItem v-for="(im,i) in imgs" :key="i">
                <img @click="gotoHome(i)" :src="im" alt="" class="g-img">
            </SwipeItem>
        </SwipeDemo>
    </div>
</template>

<script>
export default {
    mounted(){
        // localStorage object 
        if(localStorage.pcount){
            localStorage.pcount++;
            if(localStorage.pcount>3){
                 this.$router.push({name:'home'})
            }
        }else{
            localStorage.pcount = 1;
        }
    },
    methods:{
        gotoHome(idx){
            if(idx==this.imgs.length-1){
                this.$router.push({name:'home'})
            }
        }
    },
    data(){
        return {
            imgs:[
                require("@/assets/images/img1.jpg"),
                require("@/assets/images/img2.jpg"),
                require("@/assets/images/img3.jpg"),
                require("@/assets/images/img4.jpg"),
            ],
            swiperOptions:{
                loop:false,
                autoplay:{
                    delay:1500
                }
            }
        }
    }
}
</script>

<style scoped>
#guide,#swipeone, .g-img{
    width:100%;
    height:100%;
}
</style>


